:root {
  --c-bg: #000;
  --c-text: #f2f2f2;
  --fs-text: 14px;
  --fs-heading: 18px;
  --fs-title: 36px;
  --column: 146px;
  --margin: 80px;
}

*, :before, :after {
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--c-bg);
  color: var(--c-text);
  font-family: Cinzel, serif;
  font-weight: 400;
  font-size: var(--fs-text);
  font-feature-settings: "liga";
  text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  display: block;
}

body.active {
  background-color: var(--c-text);
  color: var(--c-bg);
}

body::-webkit-scrollbar {
  display: none;
}

div {
  display: block;
}

#PageWrapper {
  z-index: 1;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

main {
  display: block;
}

#PageWrapper .page {
  width: 100%;
  height: 100%;
}

.main {
  width: 100%;
  height: 100%;
  position: relative;
}

.main_header {
  width: 90%;
  padding: 0 var(--margin);
  justify-content: space-between;
  align-items: flex-end;
  display: flex;
  position: absolute;
  top: calc(30% - 10px);
  left: 0;
  transform: translateY(-100%);
}

.main_title {
  letter-spacing: -.02rem;
  margin-left: 80px;
  font-size: 50px;
  line-height: .9;
}

.main_title .line3 {
  text-align: right;
}

.main_desc {
  left: calc(var(--margin)  + var(--column) * 1.3);
  font-size: 14px;
  line-height: 1.5;
  position: absolute;
  top: calc(241px + 35vh);
  transform: translateY(100%);
  text-transform: capitalize !important;
}

img, svg, video {
  width: 100%;
  display: block;
}

.slider-wrap {
  width: 90%;
  height: 320px;
  cursor: grab;
  border-left: 2px solid #000;
  border-right: 2.5px solid #000;
  position: absolute;
  top: 30%;
  left: 5%;
}

.image_one {
  width: 569px;
}

.image_two {
  width: 690px;
}

.image_three {
  width: 601px;
}

.image_four {
  width: 555px;
}

.image_five {
  width: 569px;
}

img {
  filter: grayscale();
  border: 1px solid #000;
}

img:hover {
  filter: none;
}

.slider {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: scroll;
}

.slider-inner {
  width: 3000px;
  height: 100%;
  column-gap: 10px;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.item {
  height: 100%;
  background-color: #cdcdcd;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.progress-bar {
  height: 5px;
  width: 100%;
  border: none;
  border-radius: 10px;
  position: absolute;
  bottom: -20px;
  left: 0;
}

.prog-bar-inner {
  width: 0%;
  height: 100%;
  background-color: none;
  position: absolute;
  top: 0;
  left: 0;
}

.slider::-webkit-scrollbar {
  display: none;
}

.slider {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

#toggle {
  width: 70px;
  height: 35px;
  cursor: crosshair;
  background: #222;
  border-radius: 35px;
  transition: all .5s;
  display: block;
  position: relative;
  box-shadow: inset 0 4px 10px #0000001a, inset 0 2px 4px #0000001a, inset 0 -4px 4px #0000001a;
}

#toggle.active {
  background: var(--c-text);
  box-shadow: inset 0 4px 10px #0000001a, inset 0 2px 4px #0000001a, inset 0 -4px 4px #0000001a;
}

#toggle .indicator {
  width: 35px;
  height: 35px;
  background: linear-gradient(#444, #222);
  border-radius: 50%;
  transition: all .5s;
  position: absolute;
  top: 0;
  left: 0;
  transform: scale(.9);
  box-shadow: 0 2px 4px #00000080, inset 0 1px 2px #fff3, inset 0 -1px 2px #fff3;
}

#toggle.active .indicator {
  background: linear-gradient(#eaeaea, #f9f9f9);
  left: 35px;
  box-shadow: 0 2px 2px #0000001a, inset 0 1px 2px #fff, inset 0 -1px 2px #fff;
}

header.active {
  color: #000;
  background-color: #fff;
}

.siteHeader {
  z-index: 2;
  cursor: crosshair;
  position: fixed;
  bottom: 40px;
  left: 80px;
}

.nav-toggle {
  transform-origin: 0 100%;
  font-size: 16px;
  transform: translateX(14px)rotate(-90deg);
}

.nav {
  height: 100%;
  width: 100%;
  z-index: 500;
  background-color: #000;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.nav.open {
  transform: translateX(0%);
}

.nav .siteNav_title {
  letter-spacing: -.02rem;
  font-size: 36px;
  line-height: .9;
  position: absolute;
  top: 60px;
  left: 80px;
}

.nav .close {
  cursor: crosshair;
  transform-origin: 0 100%;
  font-size: 16px;
  position: absolute;
  bottom: 40px;
  left: 80px;
  transform: translateX(14px)rotate(-90deg);
}

.siteNav_title .line3 {
  text-align: right;
}

ol, ul {
  list-style-type: none;
}

.siteNav_pages {
  left: calc(var(--column) * 2.3);
  position: absolute;
  top: calc(90px + 15vh);
}

li {
  text-align: -webkit-match-parent;
  display: list-item;
}

.siteNav_pages li .label {
  font-size: 18px;
}

.siteNav_pages li a {
  align-items: baseline;
  gap: 0 1rem;
  transition-property: opacity;
  transition-duration: .3s;
  transition-timing-function: cubic-bezier(.3, .1, .1, 1);
  display: flex;
}

a {
  color: inherit;
  text-decoration: none;
}

.siteNav_pages li .name {
  letter-spacing: -.02rem;
  font-size: 60px;
}

.siteNav_pages li:hover {
