}

.siteNav_glimpse {
  left: calc(var(--column) * 1.2);
  letter-spacing: .04rem;
  opacity: .7;
  font-size: 17px;
  line-height: 1.4;
  position: absolute;
  top: calc(321px + 35vh);
}

